body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  font-family: 'Nunito', sans-serif;
  font-size: 16px;
}

.applicationList-panel {
  margin: 10px;
}

.form-control {
  padding: 10px;
  margin: 2% 0;
  border-style: solid;
}

.card {
  margin: 1% 0;
}

.card-header {
  border-bottom: none;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer
}

.navbar-brand {
  margin: 0 auto;
  padding: auto 0;
}

.gradient {  
    background-image: -webkit-gradient(linear, left bottom, left top, from(#4c2d79), to(#262262));  
    background-image: -webkit-linear-gradient(bottom, #4c2d79, #262262);  
    background-image: linear-gradient(0deg, #4c2d79, #262262);
}
